import React, { useEffect, useState } from "react";
import { Navigate } from 'react-router-dom'
import { authService } from '../services/authService'
import '../css/main.css';

export default function Home() {
    const [redirectTo, setRedirectTo] = useState(null);
    const [userName, setUserName] = useState('');

    const navbar = document.querySelector('.navbar');

    if (navbar) navbar.style.display = "flex";

    useEffect(() => {
        async function redirect() {
            const loggedUser = await authService.getUserInLocalStorange();
            if (!loggedUser) setRedirectTo('/login');
        };
        redirect();

        async function getUser() {
            const { user } = await authService.getUserInLocalStorange()
            if (user) setUserName(user);
        }
        getUser();
    }, [])

    if (redirectTo) {
        return (<Navigate to={redirectTo} />)
    }

    const contactDeveloper = (event) => {
        event.preventDefault();
        const URL = `https://api.whatsapp.com/send?phone=5535999554534`;
        const redirect = window.encodeURIComponent();
        window.open(URL + redirect, "_blank")
    }

    return (
        <div className="container-main">
            <h2 className="mb-3 title-page">Ol, {userName ? userName : 'Seja Bem Vindo'}!</h2>
            <p>Este  o <strong>ARV - Controll &copy;</strong>, um Sistema de Gerenciamento de Vendas e Agenda.</p>
            <p>Com ele  simples fazer o gerenciamento de suas vendas, com uma tela dedicada para voc registrar todas as suas vendas, e outra com a listagem delas.</p>
            <p>H tambm uma tela dedicada para sua agenda, com ela voc consegue criar um novo horrio para um determinado cliente, especificando o procedimento,
                data, hora, preo e mais.
            </p>
            <div className="container">
                <h4>O sistema tem as seguintes telas:</h4>
                <ul>
                    <li><h5 className="title-page">Home:</h5>
                        <p> a tela inicial do sistema. Nela voc encontrar uma breve documentao explicando todos os pontos do sistema.</p>
                    </li>
                    <li><h5 className="title-page">Nova venda:</h5>
                        <p>Nessa tela voc pode registrar uma nova venda, com os seguintes campos: <strong>descrio, preo e data.</strong> Todos
                            campos so obrigatrios.</p>
                    </li>
                    <li><h5 className="title-page">Vendas</h5>
                        <p>Aqui voc consegue visualizar todas as suas vendas registradas. <br />
                            Ao abrir essa tela aparecer todas as vendas realizadas no dia. <br />
                            Voc pode filtrar suas vendas por data, preenchendo o primeiro perodo para realizar as buscas apenas da primeira data escolhida, ou preenchendo
                            as duas datas para fazer uma busca pelo perodo da primeira data at a segunda data. <br />
                            Ser retornada uma tabela com todas as vendas encontradas de acordo com as datas selecionadas. Ao final da tela, tem um somatria
                            de todas as vendas que esto na tabela, facilitando assim a visualizao dos seus ganhos.
                        </p>
                    </li>
                    <li><h5 className="title-page">Agenda</h5>
                        <p>Nessa tela dedicada, voc encontrar todas as opes relacionadas  agenda. Aqui voc pode criar um novo horrio para um determinado cliente. <br />
                            Voc pode adicionar um novo horrio clicando no campo <strong>Adicionar Novo Horrio</strong>. <br />
                            Para visualizar os horrios da sua agenda, basta selecionar o dia que deseja, e ser retornada uma lista com todos os horrios cadastrados. <br />
                            Voc ter trs opes em cada horrio: <strong>Finalizar, Editar, Excluir e Confirmar horrio pelo WhatsApp.</strong>
                        </p>
                        <ul>
                            <li><strong>Finalizar:</strong>
                                <p>Ao clicar em "Finalizar", o horrio ser registrado como uma nova venda, e ser excludo da sua agenda, no podendo ser restaurado.</p>
                            </li>
                            <li><strong>Editar:</strong>
                                <p>Ao clicar em "Editar", um novo modal ir aparecer, com as informaes do horrio selecionado onde voc poder editar as informaes do moesmo.</p>
                            </li>
                            <li><strong>Excluir:</strong>
                                <p>Ao clicar em "Excluir", o horrio ser excludo da sua agenda e no poder ser recuperado novamente. Ele no ser salvo como uma nova venda.</p>
                            </li>
                            <li><strong>Confirmar horrio pelo WhatsApp:</strong>
                                <p>Caso voc adicione um nmero de telefone quando estiver cadastrando um novo horrio, essa opo ir aparecer. Ao clicar nela, voc ser
                                    redirecionado para o WhatsApp e iniciar uma conversa com o cliente, com uma mensagem de texto pr-definida que ser preenchidas com as seguintes informaes:
                                    <strong> nome do cliente, data e hora.</strong>
                                </p>
                            </li>
                        </ul>
                    </li>
                    <li><h5 className="title-page">Sair</h5>
                        <p>Ao clicar nessa opo, voc sair do sistema, sendo necessrio fazer login novamente para entrar. Uma vez fora do sistema, no  possvel acessar
                            nenhuma informao dentro dele, a no ser que esteja logado.
                        </p>
                    </li>
                </ul>
            </div>
            <div className="container mb-5">
                <h5 className="title-page">Ajuda</h5>
                <p>Caso precise de ajuda ou queira tirar dvidas sobre o sistema, entre em contato com o desenvolvedor</p>
                <button type="button" onClick={e => contactDeveloper(e)} className="btn btn-success col">Entrar em contato</button>
            </div>
            <p className="mt-5 mb-3 text-muted text-center">ARV - Controll &copy; 2021-2022</p>
        </div>
    )
}