import axios from 'axios';

const API_URL = process.env.REACT_APP_BASE_URL;

const clientService = {
    async newClient(id_user, name, email, phone) {
        const URI = `${API_URL}/new-client`;
        try {
            const { data } = await axios.post(URI, {
                id_user: id_user,
                name: name,
                email: email,
                phone: phone
            })
            return data
        } catch (error) {
            return error
        }
    },

    async updateClient(id_user, id, name, email, phone) {
        const URI = `${API_URL}/update-client`;
        try {
            const { data } = await axios.post(URI, {
                id_user: id_user,
                id: id,
                name: name,
                email: email,
                phone: phone
            })
            return data
        } catch (error) {
            return error
        }
    },

    async findClient(id_user, id) {
        const URI = `${API_URL}/find-client`;
        try {
            const { data } = await axios.post(URI, {
                id_user: id_user,
                id: id,
            })
            return data
        } catch (error) {
            return error
        }
    },

    async findAllClient(id_user) {
        const URI = `${API_URL}/find-all-client`;
        try {
            const { data } = await axios.post(URI, {
                id_user: id_user,
            })
            return data
        } catch (error) {
            return error
        }
    },

    async deleteClient(id_user, id) {
        const URI = `${API_URL}/delete-client`;
        try {
            const { data } = await axios.post(URI, {
                id_user: id_user,
                id: id,
            })
            return data
        } catch (error) {
            return error
        }
    }
}

export { clientService }