import React, { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import { TopModal } from '../components/topModal';
import { authService } from '../services/authService';
import { clientService } from '../services/clientService';
import { Alert } from "react-bootstrap";
import InputMask from "react-input-mask";
import '../css/main.css';

export default function Clients() {
    const [redirectTo, setRedirectTo] = useState(null);
    const [id_user, setId_user] = useState(null);
    const [name, setName] = useState(null);
    const [email, setEmail] = useState(null);
    const [phone, setPhone] = useState(null);
    const [id, setId] = useState(null);

    const [addUser, setAddUser] = useState(null);

    const [clients, setClients] = useState(null);

    const [edit, setEdit] = useState(null);
    const [list, setList] = useState('');

    const [search, setSearch] = useState('');

    useEffect(() => {
        async function redirect() {
            const loggedUser = await authService.getUserInLocalStorange();
            if (!loggedUser) setRedirectTo('/login');
        };
        redirect();

        async function getUserId() {
            const userId = await authService.getUserIdInLocalStorange(id_user);
            setId_user(userId);
        }
        getUserId();

        getClients(id_user);
    }, [])

    async function getClients() {
        try {
            const userId = await authService.getUserIdInLocalStorange();
            const clients = await clientService.findAllClient(userId);

            if (clients.message === "Network Error") {
                setAddUser([])
                return
            }

            if (clients) {
                setClients(clients)
            }

            if (!clients.length) {
                setClients([]);
            }
        } catch (error) {
            setClients([]);
        }
    }

    if (redirectTo) {
        return (<Navigate to={redirectTo} />)
    }

    const clearStates = () => {
        setEdit(false);
        setName('');
        setEmail('');
        setPhone('');
    }

    const newClient = async (event) => {
        event.preventDefault();

        try {
            if (!id_user && !phone) {
                setAddUser(false);
            }

            const addClient = await clientService.newClient(id_user, name, email, phone);
            console.log(addClient)
            if (addClient.type === 'success') {
                setAddUser(true);
                clearStates();
                getClients(id_user);
            }

            if (addClient.type === 'inputs_invalids') {
                setAddUser(false);
            }
        } catch (error) {
            setAddUser([])
        }
    }

    const updateClient = async (event) => {
        event.preventDefault();
        const updateClient = await clientService.updateClient(id_user, id, name, email, phone);
        console.log()

        if (updateClient.type === 'success') {
            setAddUser(true);
            clearStates();
            getClients(id_user);
        }

        if (updateClient.type === 'inputs_invalids') {
            setAddUser(false);
        }

        setEdit(false);
        clearStates();
    }

    const deleteClient = async (event, id) => {
        event.preventDefault();
        const deleteClient = await clientService.deleteClient(id_user, id)

        if (deleteClient.type === 'success') {
            clearStates();
            getClients(id_user);
        }
    }

    const activeEditClient = async (event, id, name, email, phone) => {
        event.preventDefault();
        setEdit(true);
        setId(id)
        setName(name);
        setEmail(email);
        setPhone(phone);
    }

    if (addUser === false || addUser === true) {
        setTimeout(() => setAddUser(null), 5000)
    }
    return (
        <div className="container-main">
            <h3 className="title-page">Clientes</h3>
            <button type="button" className="btn btn-outline-primary col" data-toggle="modal" data-target="#modalClient">
                Adicionar Novo Cliente
            </button>

            <TopModal
                className="btn btn-danger"
                id="delete-client"
                title="Excluir cliente?"
                body="Tem certeza que deseja excluir esse cliente?"
                click={e => deleteClient(e, id)}
                button="Excluir"
            />

            <div className="modal fade" id="modalClient" tabIndex="-1" role="dialog" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            {
                                edit
                                    ? <h5 className="modal-title" >Editar Cliente</h5>
                                    : <h5 className="modal-title" >Novo Cliente</h5>
                            }
                            <button type="button" className="close" onClick={clearStates} data-dismiss="modal" aria-label="Fechar">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <form>
                                <label htmlFor="name">Cliente</label>
                                <input
                                    type="text"
                                    className="form-control mb-3"
                                    id="client"
                                    value={name}
                                    onChange={e => setName(e.target.value)}
                                    placeholder={"Digite o nome do cliente"}
                                    required={true} />
                                <label htmlFor="name">E-mail</label>
                                <input
                                    type="text"
                                    className="form-control mb-2"
                                    id="procedure"
                                    value={email}
                                    onChange={e => setEmail(e.target.value)}
                                    placeholder={"Digite o email"}
                                    required={false} />
                                <label htmlFor="name">Telefone</label>
                                <InputMask
                                    type="text"
                                    format="currency"
                                    className="form-control"
                                    value={phone}
                                    id="contact"
                                    mask="(99) 9 9999-9999"
                                    onChange={e => setPhone(e.target.value)}
                                    placeholder="(__) _ ____ - ____"
                                    required={true} />
                                <div className="modal-footer mt-4">
                                    <button type="reset" onClick={clearStates} className="btn btn-outline-secondary col" data-dismiss="modal">Fechar</button>
                                    {
                                        edit
                                            ? <button type="button" onClick={updateClient} className="btn btn-primary col">Salvar</button>
                                            : <button type="button" onClick={newClient} className="btn btn-primary col">Adicionar</button>
                                    }
                                </div>

                                {addUser === true
                                    ? <Alert variant="success">Cliente adicionado com sucesso.</Alert>
                                    : null
                                }
                                {addUser === false
                                    ? <Alert variant="danger">Preencha os campos corretamente.</Alert>
                                    : null
                                }
                            </form>
                        </div>
                    </div>
                </div>
            </div>


            <div>
                <h4 className="mt-4 mb-3">Seus clientes</h4>
                <div>
                    {clients
                        ? <div>
                            {
                                clients.map(item => {
                                    return (
                                        <div className="card p-2 mb-3">
                                            <h6 className="card-header text-primary font-weight-bold mb-2">
                                                Cliente: <small className="text-muted h6 mb-3">
                                                    {item.name}
                                                </small>
                                            </h6>
                                            <div className="list-group-item p-3">
                                                {
                                                    item.email
                                                        ? <h6 className="card-subtitle text-primary font-weight-bold border-bottom pb-2 pt-2 mb-3 ">
                                                            E-mail: <small className="text-muted h6 mb-3">
                                                                {item.email}
                                                            </small>
                                                        </h6>
                                                        : null
                                                }
                                                <h6 className="card-subtitle text-primary font-weight-bold pt-2 ">
                                                    Telefone: <small className="text-muted h6 mb-3">
                                                        {item.phone}
                                                    </small>
                                                </h6>
                                            </div>
                                            <div className="form-row mt-3">
                                                <button type="button" className="col m-1 btn btn-outline-primary"
                                                    data-toggle="modal" data-target="#modalClient"
                                                    onClick={e => activeEditClient(e, item._id, item.name, item.email, item.phone)}
                                                >
                                                    Editar</button>
                                                <button type="button" className="col m-1 btn btn-outline-danger"
                                                    data-toggle="modal" data-target="#delete-client"
                                                    onClick={e => setId(item._id)}
                                                >
                                                    Excluir</button>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                        : null}
                </div>

            </div>

        </div >
    )
}
