import React, { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import { authService } from '../services/authService';
import { salesService } from '../services/salesService'
import { mask } from '../services/maskMoney';
import { Alert } from "react-bootstrap";
import '../css/main.css';

export default function NewSale() {
    const [redirectTo, setRedirectTo] = useState(null);
    const [description, setDescription] = useState('');
    const [price, setPrice] = useState('');
    const [sale, setSale] = useState('');
    const [date, setDate] = useState('');
    const [client, setClient] = useState('');
    const [user_id, setUser_id] = useState('');
    const [serverError, setServerError] = useState(null);

    useEffect(() => {
        async function redirect() {
            const loggedUser = await authService.getUserInLocalStorange();
            if (!loggedUser) setRedirectTo('/login')
        }
        redirect();

        async function getUserId() {
            const userId = await authService.getUserIdInLocalStorange();
            setUser_id(userId);
        }
        getUserId();
    }, [])

    if (redirectTo) {
        return (<Navigate to={redirectTo} />)
    }

    const saveSale = async (event) => {
        event.preventDefault();
        const newSaleResponse = await salesService.newSale(user_id, description, client, price, date)
        try {
            if (newSaleResponse.type === 'success') {
                setDescription('')
                setPrice('')
                setDate('')
                setSale(true)
            }

            if (!newSaleResponse) {
                setServerError(true);
            }
        } catch (error) {
            setServerError(true);
        }
    }

    if (sale === true) {
        setTimeout(() => setSale(null), 5000)
    }
    if (serverError) {
        setTimeout(() => setServerError(null), 5000)
    }

    return (
        <div className="container-main">
            <h3 className="title-page">Registrar Nova Venda</h3>
            <form onSubmit={saveSale} className="form-sale">
                <div className="card p-3">
                    <div className="form-group">
                        <label htmlFor="name">Cliente</label>
                        <input
                            type="text"
                            className="form-control"
                            value={client}
                            onChange={e => setClient(e.target.value)}
                            id="client"
                            placeholder="Digite o nome do cliente"
                            required={false} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="name">Descrio</label>
                        <input
                            type="text"
                            className="form-control"
                            value={description}
                            onChange={e => setDescription(e.target.value)}
                            id="name"
                            placeholder="Digite a descrio"
                            required={true} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="price">Preo</label>
                        <input
                            type="text"
                            format="currency"
                            className="form-control"
                            value={price}
                            onChange={ev => {
                                let val = ev.target.value;
                                const { maskedValue } = mask(val, 2, ",", ".", false, "R$");
                                setPrice(maskedValue);
                            }}
                            id="price"
                            placeholder="Digite o preo"
                            required={true} />
                    </div>
                    <div className="form-group">
                        <label htmlFor="price">Data</label>
                        <input
                            type="date"
                            className="form-control"
                            id="date"
                            value={date}
                            onChange={e => setDate(e.target.value)}
                            required={true} />
                        <small className="form-text text-muted">
                            Selecione a data da venda.
                        </small>
                    </div>
                    <button type="submit" className="btn btn-primary btn-entrar">Salvar</button>
                    <div className="mt-3">
                        {sale === true && (<Alert variant="success">Venda registrada com sucesso!</Alert>)}
                        {sale === false && (<Alert variant="danger">Erro ao registrar venda!</Alert>)}
                        {serverError && (<Alert variant="danger">No foi possvel registrar a venda!</Alert>)}
                    </div>
                </div>
            </form>
        </div>
    )
}