import React, { useState, useEffect } from "react";
import { Alert } from "react-bootstrap";
import { Navigate } from 'react-router-dom';
import '../css/main.css';
import { authService } from "../services/authService";
import logo from '../img/raise-value.png';

function LoginPage() {
    const [user, setUser] = useState('');
    const [password, setPassword] = useState('');
    const [redirectTo, setRedirectTo] = useState(null);
    const [credentials, setCredentials] = useState(null);
    const [serverError, setServerError] = useState(null);

    useEffect(() => {
        const navbar = document.querySelector('#menu');
        if (navbar) navbar.style.display = "none";
    }, [])

    const login = async (event) => {
        event.preventDefault();
        const sucess = 'success';
        const errorTypes = ['invalid_credentials', 'unauthorized'];
        const reqLogin = await authService.sendLogin(user, password);

        if (!reqLogin.status || !reqLogin || !reqLogin.data.type) {
            setServerError(true);
        }

        if (reqLogin.data.type === sucess) {
            authService.saveUserInLocalStorange({ user: reqLogin.data.user, user_id: reqLogin.data.user_id })
            setRedirectTo('/');
        }

        if (reqLogin.status === 200 && errorTypes.includes(reqLogin.data.type)) {
            setCredentials(false);
        }

    }

    if (redirectTo) {
        return (
            <Navigate to={redirectTo} />
        )
    }

    if (credentials === false) {
        setTimeout(() => setCredentials(null), 5000)
    }

    if (serverError === true) {
        setTimeout(() => setServerError(null), 5000)
    }

    return (
        <div className="container-main">
            <div className="text-center">
                <img className="img-logo" alt="" src={logo}></img>
            </div>
            <div className="form-login">
                <div className="card-body card-login">
                    <form onSubmit={login}>
                        <div className="text-center">
                            <h2 className="text-center">Faa seu Login</h2>
                            <p className="text-primary font-weight-bold">Seja Bem-Vindo!
                                <small className="form-text text-muted mb-2">
                                    Faa Login para entrar no sistema.
                                </small>
                            </p>
                        </div>
                        <div className="form-group">
                            <label htmlFor="name">Usurio</label>
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <div className="input-group-text">@</div>
                                </div>
                                <input
                                    type="text"
                                    className="form-control"
                                    value={user}
                                    onChange={e => setUser(e.target.value)}
                                    id="name"
                                    placeholder="Digite o nome do usurio"
                                    required={true} />
                            </div>
                        </div>
                        <div className="form-group">
                            <label htmlFor="password">Senha</label>

                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <div className="input-group-text">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-key" viewBox="0 0 16 16">
                                            <path d="M0 8a4 4 0 0 1 7.465-2H14a.5.5 0 0 1 .354.146l1.5 1.5a.5.5 0 0 1 0 .708l-1.5 1.5a.5.5 0 0 1-.708 0L13 9.207l-.646.647a.5.5 0 0 1-.708 0L11 9.207l-.646.647a.5.5 0 0 1-.708 0L9 9.207l-.646.647A.5.5 0 0 1 8 10h-.535A4 4 0 0 1 0 8zm4-3a3 3 0 1 0 2.712 4.285A.5.5 0 0 1 7.163 9h.63l.853-.854a.5.5 0 0 1 .708 0l.646.647.646-.647a.5.5 0 0 1 .708 0l.646.647.646-.647a.5.5 0 0 1 .708 0l.646.647.793-.793-1-1h-6.63a.5.5 0 0 1-.451-.285A3 3 0 0 0 4 5z" />
                                            <path d="M4 8a1 1 0 1 1-2 0 1 1 0 0 1 2 0z" />
                                        </svg>
                                    </div>
                                </div>
                                <input
                                    type="password"
                                    className="form-control"
                                    value={password}
                                    onChange={e => setPassword(e.target.value)}
                                    id="password"
                                    placeholder="Digite a senha"
                                    required={true} />
                            </div>

                        </div>
                        <button type="submit" className="btn btn-primary btn-entrar mt-4 p-2">Entrar</button>
                    </form>
                    <div className='credentials'>
                        {credentials === false
                            ? <Alert variant="danger">Usurio ou Senha Incorretos!</Alert>
                            : null
                        }
                        {serverError === true
                            ? <Alert variant="danger">Erro ao processar sua requisio!</Alert>
                            : null
                        }
                    </div>
                </div>
            </div>
            <p className="mt-5 mb-3 text-muted text-center">ARV - Controll &copy; 2021-2022</p>
        </div>
    )
}

export { LoginPage }