import React, { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import { Alert } from "react-bootstrap";

import { authService } from '../services/authService'

import { salesService } from '../services/salesService';

export default function Sales() {
    const [redirectTo, setRedirectTo] = useState(null);
    const [date1, setDate1] = useState('');
    const [date2, setDate2] = useState('');
    const [sales, setSales] = useState([]);
    const [errorSales, setErrorSales] = useState(null);
    const [serverError, setServerError] = useState(null);

    const [user_id, setUser_id] = useState('');

    useEffect(() => {
        async function redirect() {
            const loggedUser = await authService.getUserInLocalStorange();
            if (!loggedUser) setRedirectTo('/login')
        }
        redirect();

        async function getUserId() {
            const userId = await authService.getUserIdInLocalStorange(user_id);
            setUser_id(userId);
        }
        getUserId();

        async function getSalesTodayResponse() {
            const userId = await authService.getUserIdInLocalStorange();
            const salesResponse = await salesService.getSalesToday(userId);

            setDate1(new Date())

            if (salesResponse) {
                setSales(salesResponse)
            }
        }
        getSalesTodayResponse();
    }, [])

    const getSalesInPeriodResponse = async (event) => {
        event.preventDefault();
        const errorTypes = ['inputs_invalids'];
        if (date1 === '' || !date1) {
            setErrorSales(true)
        }
        const responseSales = await salesService.getSalesInPeriod(user_id, date1, date2);


        if (responseSales.length > 0) {
            setSales(responseSales)
            setErrorSales(false);
        }

        if (!responseSales || responseSales.length === 0) {
            setErrorSales(true);
            setSales([]);
        }

        if (errorTypes.includes(responseSales.type)) {
            setSales([])
        }

        if (responseSales.message === "Network Error") {
            setServerError(true);
        }

    }

    if (errorSales === false || errorSales === true) {
        setTimeout(() => setErrorSales(null), 5000)
    }

    if (serverError === false || serverError === true) {
        setTimeout(() => setServerError(null), 5000)
    }

    if (redirectTo) {
        return (<Navigate to={redirectTo} />)
    }

    let total;

    if (sales.length > 0) {
        total = sales.filter(item => !!item).map((item) => parseInt(item.price.substring(2).replace(",", "").replace(".", "")) / 100).reduce((acc, item) => acc + item, 0).toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })
    }

    return (
        <div className="container-main">
            <h3 className="title-page">Suas vendas</h3>
            <div className="card mb-4 p-3">
                <h4 className="mb-3">Selecione o perodo</h4>
                <form onSubmit={e => getSalesInPeriodResponse(e)} >
                    <div className="form-row d-flex align-items-center">
                        <div className="col mb-3">
                            <label>Primeira data</label>
                            <input
                                type="date"
                                className="form-control"
                                id="date1"
                                onChange={e => setDate1(e.target.value)}
                                required={true} />
                            <small className="form-text text-muted">
                                Selecione a primeira data.
                            </small>
                        </div>
                        <div className="col mb-3">
                            <label>Segunda data</label>
                            <input
                                type="date"
                                className="form-control"
                                id="date2"
                                onChange={e => setDate2(e.target.value)}
                                required={false} />
                            <small className="form-text text-muted">
                                Selecione a segunda data.
                            </small>
                        </div>
                        <button type="submit" className="col-md-2 btn btn-primary mb-1">Selecionar</button>
                    </div>
                </form>
            </div>
            {errorSales === true
                ? <Alert variant="danger">Nenhuma venda encontrada nesse perodo.</Alert>
                : null
            }
            {errorSales === false
                ? <Alert variant="success">Perodo atualizado.</Alert>
                : null
            }
            {serverError === true
                ? <Alert variant="danger">Nenhum dado foi retornado.</Alert>
                : null
            }
            {sales.length > 0 ? (
                <div className="table-responsive-sm">
                    <div className="d-inline"><strong>Perodo: </strong>{
                        (date1 || date2) && (
                            <div className="d-inline">
                                {date1 && new Date(date1).toLocaleDateString('pt-BR', { timeZone: 'UTC' })}
                                {date2 && (" - " + new Date(date2).toLocaleDateString('pt-BR', { timeZone: 'UTC' }))}
                            </div>
                        )}
                    </div>
                    <table className="table table-bordered mt-3">
                        <thead className="thead-dark header-table">
                            <tr>
                                <th>Cliente</th>
                                <th>Descrio</th>
                                <th>Data</th>
                                <th>Preo</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                sales.map((item) => {
                                    return (
                                        <tr key={Math.random()}>
                                            <td className="row-table">{item.client}</td>
                                            <td className="row-table">{item.description}</td>
                                            <td className="row-table">{
                                                new Date(item.date)
                                                    .toLocaleDateString('pt-BR', { timeZone: 'UTC' })
                                            }</td>
                                            <td className="row-table">{item.price}</td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                    <div className="inline">
                        <p><strong>Quantidade de vendas:</strong> {sales.length > 0 ? sales.length : null}</p>
                    </div>
                    <div className="total-sale">
                        <h4>Total:</h4>
                        <h4>
                            {total.length > 0 ? total : null}
                        </h4>
                    </div>
                </div>
            ) : (
                <h4>Nenhuma venda encontrada.</h4>
            )}
        </div>
    )
}
