import React, { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import '../css/main.css';

import { authService } from '../services/authService';
import { Alert } from "react-bootstrap";
import { mask } from '../services/maskMoney';
import InputMask from "react-input-mask";

import { scheduleService } from '../services/scheduleService';
import { salesService } from '../services/salesService';
import { clientService } from '../services/clientService';
import { TopModal } from '../components/topModal';

export default function Schedule() {
    const [redirectTo, setRedirectTo] = useState(null);
    const [schedules, setSchedule] = useState([]);
    const [id, setId] = useState('')
    const [client, setClient] = useState('');
    const [procedure, setProcedure] = useState('');
    const [dateNewSchedule, setDateNewSchedule] = useState(null);
    const [time, setTime] = useState(null);
    const [price, setPrice] = useState('');
    const [contact, setContact] = useState('');
    const [date, setDate] = useState(null);
    const [errorSchedule, setErrorSchedule] = useState(null);
    const [errorClientSchedule, setErrorClientSchedule] = useState(null);
    const [serverError, setServerError] = useState(null);
    const [finalitySchedule, setFinalitySchedule] = useState(null);
    const [edit, setEdit] = useState(false);
    const [deleted, setDeleted] = useState(null);
    const [user_id, setUser_id] = useState('');

    const [clientSaves, setClientSaves] = useState(null);

    useEffect(() => {
        async function redirect() {
            const loggedUser = await authService.getUserInLocalStorange();
            if (!loggedUser) setRedirectTo('/login')
        }
        redirect();

        async function getUserId() {
            const userId = await authService.getUserIdInLocalStorange();
            setUser_id(userId);
        }
        getUserId();

        async function getClients() {
            const userId = await authService.getUserIdInLocalStorange();
            const clients = await clientService.findAllClient(userId);
            setClientSaves(clients)
        }
        getClients();
    }, [])

    if (redirectTo) {
        return (<Navigate to={redirectTo} />)
    }

    const clearStates = () => {
        setEdit(false);
        setId('');
        setClient('');
        setProcedure('');
        setDateNewSchedule('');
        setTime('');
        setPrice('');
        setContact('')
    }

    const setDataClient = (event, client, phone) => {
        event.preventDefault();

        setClient(client);
        setContact(phone);
    }

    const redirectToWhatsapp = (event, client, contact, date, time) => {
        event.preventDefault();
        const dateUrl = new Date(date).toLocaleDateString('pt-BR', { timeZone: 'UTC' });
        const text = `Ol ${client}, posso confirmar o horrio que agendamos para dia ${dateUrl} s ${time} horas? \n Aguardo seu retorno!`;
        const URL = `https://api.whatsapp.com/send?phone=55${contact}&text=`;
        const redirect = window.encodeURIComponent(text);
        window.open(URL + redirect, "_blank")
    }

    const getScheduleResponse = async (event) => {
        event.preventDefault();
        const getSchedules = await scheduleService.getClientsSchedule(user_id, date);

        if (getSchedules.message === "Network Error") {
            setServerError(true);
        }

        if (getSchedules.length > 0) {
            setSchedule(getSchedules);
            setErrorSchedule(false);
        }
        if (!getSchedules) {
            setServerError(true);
        }
        if (getSchedules.length === 0) {
            setErrorSchedule(true);
            setSchedule([])
        }
    }

    const saveSchedulesResponse = async (event, id) => {
        event.preventDefault();
        const schedulesSaving = schedules.map(item => item).filter(item => item._id === id);

        const save = await salesService.newSale(
            user_id,
            schedulesSaving[0].procedure,
            schedulesSaving[0].client,
            schedulesSaving[0].price,
            schedulesSaving[0].date,
        );

        if (save.type === 'success') {
            setFinalitySchedule(true);
            await scheduleService.deleteClientSchedule(user_id, schedulesSaving[0]._id)
        }
        if (!save) {
            setFinalitySchedule(false);
        }
        await getScheduleResponse(event);
    }

    const addNewClientSchedule = async (event) => {
        event.preventDefault();
        const errorTypes = ['inputs_invalids', 'error'];
        const postClientsSchedule = await scheduleService.addClientSchedule(user_id, client, procedure, dateNewSchedule, time, price, contact);

        if (postClientsSchedule.type === 'success') {
            setErrorClientSchedule(false);
            clearStates();
        }

        if (errorTypes.includes(postClientsSchedule.type)) {
            setErrorClientSchedule(true);
        }
    }

    const activeEditSchedule = async (event, id, client, procedure, dateNewSchedule, time, price, contact) => {
        event.preventDefault();
        setId(id);
        setEdit(true);
        setClient(client);
        setProcedure(procedure);
        setDateNewSchedule(dateNewSchedule);
        setTime(time);
        setPrice(price);
        setContact(contact)
    }

    const updateClientScheduleResponse = async (event) => {
        event.preventDefault();

        const errorTypes = ['inputs_invalids', 'error'];
        const postClientsSchedule = await scheduleService.updateClientSchedule(user_id, id, client, procedure, dateNewSchedule, time, price, contact);

        if (postClientsSchedule.type === 'success') {
            setErrorClientSchedule(false);
            clearStates();
            setEdit(false)
        }

        if (errorTypes.includes(postClientsSchedule.type)) {
            setErrorClientSchedule(true);
        }

        getScheduleResponse(event)
    }

    const deleteClientScheduleResponse = async (event, id) => {
        event.preventDefault();
        const errorTypes = ['inputs_invalids'];

        const deleteSchedule = await scheduleService.deleteClientSchedule(user_id, id);

        if (deleteSchedule.type === 'success') {
            await getScheduleResponse(event);
            setDeleted(false);
        }

        if (errorTypes.includes(deleteSchedule.type)) {
            setErrorClientSchedule(true);
        }
    }

    if (errorSchedule === false || errorSchedule) {
        setTimeout(() => setErrorSchedule(null), 5000)
    }
    if (errorClientSchedule === false || errorClientSchedule) {
        setTimeout(() => setErrorClientSchedule(null), 5000)
    }
    if (serverError) {
        setTimeout(() => setServerError(null), 5000)
    }

    if (deleted === false) {
        setTimeout(() => setDeleted(null), 5000)
    }

    if (finalitySchedule) {
        setSchedule([])
        setFinalitySchedule(null)
        getScheduleResponse();
    }

    return (
        <div className="container-main">
            <TopModal
                id="finality-client-schedule"
                title="Horrio finalizado com sucesso!"
                body="Voc finalizou esse horrio e ele j foi salvo nas suas vendas!"
            />

            <TopModal
                className="btn btn-danger"
                id="delete-client-schedule"
                title="Excluir horrio?"
                body="Tem certeza que deseja excluir esse horrio?"
                click={e => deleteClientScheduleResponse(e, deleted)}
                button="Excluir"
            />

            <TopModal
                className="btn btn-primary"
                id="finalyt-schedule"
                title="Finalizar horrio?"
                data_target="#finality-client-schedule"
                body="Tem certeza que deseja finalizar esse horrio?"
                click={e => saveSchedulesResponse(e, id)}
                button="Finalizar"
            />

            <h3 className="mb-3 title-page">Agenda</h3>

            <button type="button" className="btn btn-outline-primary col mt-3" data-toggle="modal" data-target="#ExemploModalCentralizado">
                Adicionar Novo Horrio
            </button>

            <div className="modal fade" id="ExemploModalCentralizado" tabIndex="-1" role="dialog" aria-labelledby="TituloModalCentralizado" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            {edit ? (
                                <h5 className="modal-title" id="TituloModalCentralizado">Editar Horrio</h5>
                            ) : (
                                <h5 className="modal-title" id="TituloModalCentralizado">Adicionar Novo Horrio</h5>
                            )}
                            <button type="button" className="close" onClick={clearStates} data-dismiss="modal" aria-label="Fechar">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>


                        {
                            clientSaves ?
                                clientSaves.length > 0
                                    ? <div className="pl-3 pt-4 pr-3">
                                        <div className="card">
                                            <button class="btn btn-outline-primary dropdown-toggle text-left" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                Selecionar Cliente
                                            </button>
                                            <div className="dropdown-menu col" aria-labelledby="dropdownMenuButton" id="select-clients">
                                                <div className="dropdown-item">
                                                    {
                                                        clientSaves.map(item => {
                                                            return (
                                                                <button
                                                                    type="button"
                                                                    onClick={e => setDataClient(e, item.name, item.phone)}
                                                                    className="card p-2 mb-2 border-0"
                                                                >
                                                                    {item.name}
                                                                </button>
                                                            )
                                                        })
                                                    }
                                                </div>
                                            </div>
                                        </div>
                                        <small className="text-muted">Selecionar um cliente que voc tem salvo</small>
                                    </div>
                                    : null
                                : null
                        }
                        <div className="modal-body">
                            <form>
                                <label htmlFor="name">Cliente</label>
                                <input
                                    type="text"
                                    className="form-control mb-3"
                                    id="client"
                                    value={client}
                                    onChange={e => setClient(e.target.value)}
                                    placeholder={"Digite o nome do cliente"}
                                    required={true} />
                                <label htmlFor="name">Procedimento</label>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="procedure"
                                    value={procedure}
                                    onChange={e => setProcedure(e.target.value)}
                                    placeholder={"Digite o procedimento"}
                                    required={true} />
                                <small className="form-text text-muted mb-3">
                                    Ex.: Corte de cabelo, massagem, sobrancelha, etc.
                                </small>
                                <div className="form-row">
                                    <div className="form-group col">
                                        <label htmlFor="name">Data</label>
                                        <input
                                            type="date"
                                            className="form-control"
                                            id="date"
                                            value={dateNewSchedule}
                                            onChange={e => setDateNewSchedule(e.target.value)}
                                            required={true} />
                                        <small className="form-text text-muted">
                                            Selecione a data.
                                        </small>
                                    </div>
                                    <div className="form-group col">
                                        <label htmlFor="name">Horrio</label>
                                        <input
                                            type="time"
                                            className="form-control"
                                            id="time"
                                            value={time}
                                            onChange={e => setTime(e.target.value)}
                                            required={true} />
                                        <small className="form-text text-muted">
                                            Selecione horrio.
                                        </small>
                                    </div>
                                </div>
                                <div className="form-row">
                                    <div className="form-group col">
                                        <label htmlFor="name">Preo</label>
                                        <input
                                            type="text"
                                            format="currency"
                                            className="form-control mb-3"
                                            value={price}
                                            onChange={ev => {
                                                let val = ev.target.value;
                                                const { maskedValue } = mask(val, 2, ",", ".", false, "R$");
                                                setPrice(maskedValue);
                                            }}
                                            id="price"
                                            placeholder="Digite o preo"
                                            required={true} />
                                    </div>
                                    <div className="form-group col">
                                        <label htmlFor="name">Telefone</label>
                                        <InputMask
                                            type="text"
                                            format="currency"
                                            className="form-control"
                                            value={contact}
                                            id="contact"
                                            mask="(99) 9 9999-9999"
                                            onChange={e => setContact(e.target.value)}
                                            placeholder="(__) _ ____ - ____"
                                            required={true} />
                                        <small className="form-text text-muted mb-3">
                                            Telefone de contato do whatsapp.
                                        </small>
                                    </div>
                                </div>
                                <div className="modal-footer">
                                    <button type="reset" onClick={clearStates} className="btn btn-outline-secondary col" data-dismiss="modal">Fechar</button>
                                    {edit ? (
                                        <button type="button" onClick={updateClientScheduleResponse} className="btn btn-primary col">Salvar</button>
                                    ) : (
                                        <button type="button" onClick={addNewClientSchedule} className="btn btn-primary col">Adicionar</button>
                                    )}
                                </div>
                            </form>
                            {errorClientSchedule === true
                                ? <Alert variant="danger">Preencha os campos corretamente.</Alert>
                                : null
                            }
                            {errorClientSchedule === false
                                ? <Alert variant="success">Horrio adicionado com sucesso.</Alert>
                                : null
                            }
                        </div>
                    </div>
                </div>
            </div>


            <div className="card p-3 mt-4 mb-4">
                <h4>Selecione a Data</h4>
                <form onSubmit={getScheduleResponse} >
                    <div className="form-row">
                        <div className="col-7 mb-3">
                            <input
                                type="date"
                                className="form-control"
                                id="date1"
                                value={date}
                                onChange={e => setDate(e.target.value)}
                                required={true} />
                            <small className="form-text text-muted">
                                Selecione a data desejada.
                            </small>
                        </div>
                        <div className="col mb-3">
                            <button type="submit" className="col btn btn-primary">Selecionar</button>
                        </div>
                    </div>
                </form>
            </div>

            {errorSchedule === true
                ? <Alert variant="danger">Nenhum horrio agendado nessa data.</Alert>
                : null
            }
            {errorSchedule === false
                ? <Alert variant="success">Pesquisa atualizada.</Alert>
                : null
            }
            {serverError
                ? <Alert variant="danger">Nenhum horrio foi retornado.</Alert>
                : null
            }
            {deleted === false
                ? <Alert variant="success">Horrio excludo com sucesso.</Alert>
                : null
            }

            {schedules ? (
                <div>
                    <h4 className="mb-3">Seus horrios agendados</h4>
                    {
                        schedules.map(item => {
                            return (
                                <div className="mb-2 mt-3">
                                    <div className="card p-2 card-schedule">
                                        <h6 className="card-header text-primary font-weight-bold mb-2">
                                            Cliente: <small className="text-muted h6 mb-3">
                                                {item.client}
                                            </small>
                                        </h6>
                                        <div className="list-group-item">
                                            <h6 className="card-subtitle text-primary font-weight-bold border-bottom pb-2 pt-2 mb-3 ">
                                                Procedimento: <small className="text-muted h6 mb-3">
                                                    {item.procedure}
                                                </small>
                                            </h6>
                                            <div className="form-row border-bottom pb-2 mb-3">
                                                <h6 className="card-subtitle col text-primary font-weight-bold">
                                                    Data: <small className="text-muted h6 mb-3">
                                                        {new Date(item.date).toLocaleDateString('pt-BR', { timeZone: 'UTC' })}
                                                    </small>
                                                </h6>
                                                <h6 className="card-subtitle col text-primary font-weight-bold">
                                                    Horrio: <small className="text-muted h6 mb-3">
                                                        {item.time}hrs
                                                    </small>
                                                </h6>
                                            </div>
                                            <h6 className="card-subtitle mb-2 text-primary font-weight-bold border-bottom pb-2 mb-3">
                                                Preo: <small className="text-muted h6 mb-3">
                                                    {item.price.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })}
                                                </small>
                                            </h6>
                                            {item.phone && (
                                                <h6 className="card-subtitle text-primary font-weight-bold">
                                                    Telefone: <small className="text-muted h6 mb-3">
                                                        {item.phone}
                                                    </small>
                                                </h6>
                                            )}
                                        </div>
                                        <div className="mt-3">
                                            {item.phone && (
                                                <button type="submit" className="col btn btn-outline-success mb-1"
                                                    onClick={e => redirectToWhatsapp(e, item.client, item.phone, item.date, item.time)}>
                                                    Confirmar horrio pelo WhatsApp
                                                </button>
                                            )}
                                            <div className="form-row">
                                                <button type="button" className="col m-1 btn btn-outline-primary"
                                                    data-toggle="modal" data-target="#finalyt-schedule"
                                                    onClick={e => setId(item._id)}
                                                >
                                                    Finalizar</button>
                                                <button type="button" className="col m-1 btn btn-outline-primary"
                                                    data-toggle="modal" data-target="#ExemploModalCentralizado"
                                                    onClick={e => activeEditSchedule(e, item._id, item.client, item.procedure, item.date, item.time, item.price, item.phone)}>
                                                    Editar</button>
                                                <button type="button" className="col m-1 btn btn-outline-danger"
                                                    data-toggle="modal" data-target="#delete-client-schedule"
                                                    onClick={e => setDeleted(item._id)}
                                                >
                                                    Excluir</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
            ) : null}
        </div>
    )
}