import axios from 'axios';

const API_URL = process.env.REACT_APP_BASE_URL;

const scheduleService = {
    async getClientsSchedule(user_id, date) {
        const URL = `${API_URL}/schedule`;
        try {
            const { data } = await axios.post(URL, {
                id_user: user_id,
                date: date,
            });
            return data;
        } catch (error) {
            return error
        }
    },

    async addClientSchedule(user_id, client, procedure, date, time, price, contact) {
        const URL = `${API_URL}/new-schedule`;
        try {
            const { data } = await axios.post(URL, {
                id_user: user_id,
                client: client,
                procedure: procedure,
                date: date,
                time: time,
                price: price,
                contact: contact,
            });
            return data;
        } catch (error) {
            return error
        }
    },

    async updateClientSchedule(user_id, id, client, procedure, date, time, price, contact) {
        const URL = `${API_URL}/update-schedule`;
        try {
            const { data } = await axios.put(URL, {
                id_user: user_id,
                id: id,
                client: client,
                procedure: procedure,
                date: date,
                time: time,
                price: price,
                contact: contact,
            });
            return data;
        } catch (error) {
            return error
        }
    },

    async deleteClientSchedule(user_id, id) {
        const URL = `${API_URL}/delete-schedule`;
        try {
            const { data } = await axios.delete(URL, {
                params: {
                    id_user: user_id,
                    id: id,
                }
            });
            return data;
        } catch (error) {
            return error
        }
    }
}

export { scheduleService }