import axios from 'axios';
import moment from 'moment';

const API_URL = process.env.REACT_APP_BASE_URL;

const salesService = {
    async getSalesToday(user_id) {
        const URL = `${API_URL}/sales-today`;
        const date = moment(new Date()).format("YYYY-MM-DD");
        try {
            const { data } = await axios.post(URL, {
                id_user: user_id,
                date: date,
            });
            return data;
        } catch (error) {
            return error
        }
    },

    async getSalesInPeriod(user_id, date1, date2) {
        const URL = `${API_URL}/sales`;
        let date1Valid = date1;
        if(!date1) {
            date1Valid = moment(new Date()).format("YYYY-MM-DD")
        }
        try {
            const { data } = await axios.post(URL, {
                id_user: user_id,
                date1: date1Valid,
                date2: date2,
            });
            return data;
        } catch (error) {
            return error
        }
    },

    async newSale(user_id, description, client, price, date) {
        const URL = `${API_URL}/new-sale`;
        try {
            const { data } = await axios.post(URL, {
                id_user: user_id,
                description: description,
                client: client,
                price: price,
                date: date,
            });
            return data;
        } catch (error) {
            return error
        }
    },
}

export { salesService };