import axios from 'axios';
const API_URL = process.env.REACT_APP_BASE_URL;

const authService = {

    saveUserInLocalStorange(user) {
        const userData = JSON.stringify(user);
        localStorage.setItem('user-login', userData);
    },

    async getUserInLocalStorange() {
        const user = localStorage.getItem('user-login');
        if (!user) return null
        try {
            const userJson = await JSON.parse(user);
            return userJson;
        } catch (error) {
            return null;
        }
    },

    async getUserIdInLocalStorange() {
        const user = localStorage.getItem('user-login');
        const user_id = await JSON.parse(user);
        return user_id.user_id;
    },

    cleanUserInLocalStorange() {
        localStorage.removeItem('user-login');
    },

    async sendLogin(user, password) {
        const URL = `${API_URL}/login`;

        try {
            const response = await axios.post(URL, {
                user: user,
                password: password
            })
            return response
        } catch (error) {
            return error
        }
    }
}

async function getSchedule(date) {
    const URL = `${API_URL}/schedule`;
    try {
        const response = await axios.post(URL, {
            date: date
        });
        return response;
    } catch (error) {
        return error
    }
}

async function addClientSchedule(client, procedure, date, time, price, contact) {
    const URL = `${API_URL}/new-schedule`;
    let count = 1;
    try {
        const response = await axios.post(URL, {
            id: count++,
            client: client,
            procedure: procedure,
            date: date,
            time: time,
            price: price,
            contact: contact
        });
        return response;
    } catch (error) {
        return error
    }
}

async function newSale(product, price, date) {
    const URL = `${API_URL}/new-sale`;
    try {
        const response = await axios.post(URL, {
            product: product,
            price: price,
            date: date,
        });
        return response;
    } catch (error) {
        return error
    }
}


export { authService, getSchedule, addClientSchedule, newSale } 